<template>
  <Form class="from-group" ref="couponForm"
   v-slot="{errors}">
    <div class="position-relative d-flex flex-row">
      <Field
      id="code"
      name=""
      type="text"
      class="form-control"
      :class="{'is-invalid': errors[''], buttonDisabledCursor : Object.keys(errors).length > 0 }"
      placeholder=""
      :rules="couponCheck"
      v-model.lazy.trim="code"
      :disabled="false"
      >
      </Field>
      <ErrorMessage name="" class="invalid-feedback" style="position:absolute; left:14px ;bottom:-20px"></ErrorMessage>
      <button class="btn btn-primary text-white text-nowrap h-100" type="button"
      @click= "guestSendCoupon"
      :disabled="cartLength === 0 || code === '' || Object.keys(errors).length > 0 || isSend"
      :class="{buttonDisabledCursor :cartLength === 0 || code === '' || Object.keys(errors).length > 0}">
      <span v-if= "isLoading" class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
      
    </button>
    </div>
  </Form>
</template>

<script>
//  vee-validate 
import { Form, Field, ErrorMessage, configure } from 'vee-validate'
//  vee-validate 
import { localize, setLocale } from '@vee-validate/i18n'
// 
import zhTW from '@vee-validate/i18n/dist/locale/zh_TW.json'

configure({ // 
  generateMessage: localize({ zh_TW: zhTW }), //  locale
  validateOnInput: true // 
})
// 
setLocale('zh_TW')
export default {
  props: ['cartLength', 'cartLoading'],
  data () {
    return {
      code: '',
      isLoading: false,
      isSend: false,
      coupon: [],
      presentCoupon: [
        {
          coupon: 'GodCoupon',
          date: '2023/05/03'
        },
        {
          coupon: 'SuperCoupon',
          date: '2022/04/04'
        },
        {
          coupon: 'testCode',
          date: '2022/06/08'
        }
      ]
    }
  },
  watch: {
    cartLoading (newValue) {
      if (newValue === false) {
        this.isLoading = false
      } else if (newValue === true) { this.isLoading = true }
    }
  },
  components: {
    Form,
    Field,
    ErrorMessage
  },
  methods: {
    guestSendCoupon () {
      this.isLoading = true
      if (this.code === '') {
        alert('')
        this.isLoading = false
      } else {
        const sendData = { data: { code: this.code } }
        this.$http.post(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/coupon`, sendData)
          .then((res) => {
            alert(res.data.message)
            this.isLoading = false
            this.$emit('get-cart')
            this.isSend = true
          }).catch((err) => {
            console.dir(err.response.data.message)
            this.isLoading = false
          })
      }
    },
    couponCheck (value) {
      const currentDate = new Date().getTime()
      let result = true
      const currentCoupon = []
      this.presentCoupon.forEach((i) => {
        currentCoupon.push(i.coupon)
      })
      if (currentCoupon.indexOf(value) !== -1) {
        const index = currentCoupon.indexOf(value)
        if (Date.parse(this.presentCoupon[index].date) - currentDate >= 0) {
          result = true
        } else { result = '' }
      } else {
        result = ''
      }
      return result
    }
  }
}
</script>
